.exchange-rate-change{height:3rem;width:max-content}.exchange-rate-change .exchange-rate-triangle{display:inline-block;color:red;font-size:.8rem}.exchange-rate-change span{display:block}.exchange-rate-change span:first-of-type{font-size:.8rem;color:#bcbdc4}.exchange-rate-change span:nth-of-type(2){color:red;font-size:1.4rem;font-weight:500}.exchange-rate-change span:nth-of-type(2):before{font-size:.8rem;content:"▼"}.exchange-rate-change.grew .exchange-rate-triangle{display:inline-block;color:green;font-size:.8rem}.exchange-rate-change.grew span:nth-of-type(2){color:green}.exchange-rate-change.grew span:nth-of-type(2):before{content:"▲"}